import { Component } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Storage } from "@ionic/storage";
import { NavController, ToastController } from "ionic-angular";
import { Mixpanel } from "@ionic-native/mixpanel";
import { YoutubeVideoPlayer } from "@ionic-native/youtube-video-player";

import moment from "moment";
moment.locale("en");


@Component({
  selector: "page-sermons",
  templateUrl: "sermons.html"
})
export class SermonsPage {
  navigation: string = "sermons";
  segmentParams: string = "sermons";

  moment = moment;

  latestSermon: object;
  latestSermonTitle: string;
  sermons: any;
  sermonCount = 10;

  blogs = [];
  blogCount = 8;

  youtubeList: any;
  isLive: Boolean = false;

  constructor(
    public navCtrl: NavController,
    public storage: Storage,
    public http: HttpClient,
    public toastCtrl: ToastController,
    private mixpanel: Mixpanel,
    private youtube: YoutubeVideoPlayer
  ) {
    this.fetchYoutube();
    // this.fetchBlogPosts();
    this.checkIfSunday();

    this.storage.get("sermons").then(sermons => {
      if (sermons !== null) {
        this.latestSermon = sermons.shift();
        this.sermons = sermons;
      } else {
        this.http
          .get("https://realm-glory.herokuapp.com/api/sermons")
          .subscribe(sermons => {
            this.storage.set("sermons", sermons["data"]).then(() => {
              this.latestSermon = sermons["data"].shift();
              this.sermons = sermons["data"];
            });
          });
      }
      this.checkUpdates();
    });

    this.storage.get("user").then(user => {
      if (user !== null) {
        this.mixpanel.registerSuperPropertiesOnce({
          name: user.name,
          email: user.email,
          phone: user.phone
        });
      }
    });

    this.mixpanel.track("Sermons");
  }

  // fetchBlogPosts() {
  //   this.http
  //     .get("https://realm-glory.herokuapp.com/api/articles")
  //     .subscribe(blog => {
  //       this.blogs = blog["data"];
  //     });
  // }

  checkIfSunday() {
    if (moment().day() === 0) {
      if (moment().hour() >= 10 && moment().hour() <= 12) {
        this.isLive = true;
        this.segmentParams = "network";
      }
    }
  }

  onSegmentChanged(event) {
    this.segmentParams = event.value;
  }

  openSermon(sermon) {
    this.navCtrl.push("SermonDetailsPage", { sermon });
  }

  openBlog(blog) {
    this.navCtrl.push("BlogDetailsPage", { blog });
  }

  doInfiniteSermons(infiniteScroll) {
    this.sermonCount += 10;
    infiniteScroll.complete();
  }
  doInfiniteBlogs(infiniteScroll) {
    this.blogCount += 8;
    infiniteScroll.complete();
  }

  checkUpdates() {
    this.http
      .get("https://realm-glory.herokuapp.com/api/sermons")
      .subscribe(sermons => {
        this.storage.get("sermons").then(data => {
          if (sermons["data"].length > data.length) {
            this.storage.set("sermons", sermons["data"]);
            this.latestSermon = sermons["data"].shift();
            this.sermons = sermons["data"];
            this.completedSermons();
          }
        });
      });
  }

  // Network
  watchLive() {
    // this.youtube.openVideo("UCndL5ITYSamuvAMSMSVvFsQ");
    this.navCtrl.push("LivestreamPage");
  }

  openVideo(video) {
    this.youtube.openVideo(video.snippet.resourceId.videoId);
  }

  fetchYoutube() {
    this.http
      .get(
        "https://www.googleapis.com/youtube/v3/playlistItems?key=AIzaSyBT3Ac9SDjNlomU2t459qbKrf0mLtsXzeU&playlistId=PLBmiVnuyUQo2v3fnWEtl9gbRTlRvlLCGx&part=snippet,id&maxResults=20"
      )
      .subscribe(result => {
        // console.log("playlist", result);
        this.youtubeList = result["items"];
      });
  }

  completedSermons() {
    let toast = this.toastCtrl.create({
      message: "Sermons are up to date.",
      duration: 3000
    });
    toast.present();
  }
}



